.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 300;
}

p, ul{
  line-height: 180%;
}

li{
  line-height: 180%;
  text-indent: 20px;
}

pre code {
  background: #f4f6fa;
  width: 100%;
  display: block;
  padding: 20px;
  border-radius: 4px;
}


table {
  padding: 0;
  border:"1px";
  border-collapse: collapse;
}

table tr {
  background-color: white;
  margin: 0;
  padding: 0;
}

table tr:nth-child(2n) {
  background-color: #f8f8f8;
}

table tr th {
  font-weight: bold;
  border: 1px solid #cccccc;
  text-align: left;
  margin: 0;
  padding: 8px 18px;
}

table tr td {
  border: 1px solid #cccccc;
  text-align: left;
  margin: 0;
  padding: 8px 18px;
}

table tr th :first-child, table tr td :first-child {
  margin-top: 0;
}

table tr th :last-child, table tr td :last-child {
  margin-bottom: 0;
}

table thead tr:first-child {
  background: #f2f2f2;
}

@import '~highlight.js/styles/atom-one-dark.css'



