
.App-header {
  width: 30%;
  background-image: url(/image/head_bg.jpg);
  background-attachment: fixed;
  background-size: 100% 100%;
  color: white;
  height: 100vh;
  
}

.header-mask {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 100%;
  z-index: 10;
  -ms-flex-direction: column;
      flex-direction: column;
}

.header-main{
  width: 100%;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100vh;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
}

.App-logo {
  /* animation: App-logo-spin infinite 20s linear; */
  border-radius: 50%;
  border: 3px solid #FFF;
  margin: 0px 0 20px 0;
  -webkit-box-shadow: 0 0 1px 1px rgba(0,0,0,0.3);
          box-shadow: 0 0 1px 1px rgba(0,0,0,0.3);
}

.App-title {
  font-size: 1.5em;
  letter-spacing: 4px;
  color: #eee;
}

.App-subtitle {
  font-size: 1.2em;
  font-weight: 200;
  letter-spacing: 3px;
  color: #ccc;
  margin: 20px;
}

.App-cover-divider {
  width: 50%;
  margin: 20px auto;
  border-top: 1px solid rgba(255, 255, 255, 0.6);
}

.App-desp {
  font-weight: 200;
  letter-spacing: 3px;
  color: #ccc;
  margin: 30px;
}

.App-intro {
  font-size: large;
}

.menus {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.menus-item {
  width: 24px;
  height: 24px;
  margin-bottom: 20px;
  opacity: 0.8;
}
.menus-item:hover {
  opacity: 1.0;
}

.tooltip {
  margin: 10px;
  position: relative;
  display: inline-block;
}

.tooltiptext {
  visibility: hidden;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 4px;
  padding: 5px 10px;

  min-width: 120px;
  top: 100%;
  left: 50%; 
  margin-left: -60px;

  /* Position the tooltip */
  position: absolute;
  z-index: 1;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.footer{
  text-transform: uppercase;
  text-align: center;
  margin: 10px;
}



